.gear-modal {
  padding: 2rem;
  max-width: 400px;
  margin: auto;
  border-radius: 1rem;
  background: rgba(0, 10, 20, 0.9);
  border: 1px solid #ff00ff;
  color: white;
  text-align: center;
  font-family: "Orbitron", sans-serif;
}

.gear-modal p {
  margin-top: 1rem;
  line-height: 1.5;
}
